#trumbowyg-icons {
    overflow: hidden;
    visibility: hidden;
    height: 0;
    width: 0;

    #trumbowyg-icons svg {
        height: 0;
        width: 0;
    }
}

.trumbowyg-box {
    *, *::before, *::after {
        box-sizing: border-box;
    }

    svg {
        width: 17px;
        height: 100%;
        fill: #222;
    }
}

.trumbowyg-box,
.trumbowyg-editor {
    display: block;
    position: relative;
    border: 1px solid #DDD;
    width: 100%;
    min-height: 300px;
    margin: 17px auto;
}

.trumbowyg-box {
    .trumbowyg-editor {
        margin: 0 auto;
    }

    &.trumbowyg-fullscreen {
        background: #FEFEFE;
        border: none !important;
    }
}

.trumbowyg-editor,
.trumbowyg-textarea {
    position: relative;
    box-sizing: border-box;
    padding: 20px;
    min-height: 300px;
    width: 100%;
    border-style: none;
    resize: none;
    outline: none;
    overflow: auto;
}

.trumbowyg-box-blur .trumbowyg-editor {
    *, &::before {
        color: transparent !important;
        text-shadow: 0 0 7px #333;
    }
}

@media screen and (min-width: 0\0) {
    .trumbowyg-box-blur .trumbowyg-editor {
        *, &::before {
            color: rgba(200, 200, 200, 0.6) !important;
        }
    }
}

@supports (-ms-accelerator: true) {
    .trumbowyg-box-blur .trumbowyg-editor {
        *, &::before {
            color: rgba(200, 200, 200, 0.6) !important;
        }
    }
}
.trumbowyg-box-blur .trumbowyg-editor {
    img, hr {
        opacity: 0.2;
    }
}

.trumbowyg-textarea {
    position: relative;
    display: block;
    overflow: auto;
    border: none;
    white-space: normal;
    font-size: 14px;
    font-family: "Inconsolata", "Consolas", "Courier", "Courier New", sans-serif;
    line-height: 18px;

    .trumbowyg-box.trumbowyg-editor-visible & {
        height: 1px !important;
        width: 25%;
