    flex-flow: row wrap;
    width: 100%;
    min-height: 36px;
    background: #ecf0f1;
    border-bottom: 1px solid #d7e0e2;
    margin: 0;
    padding: 0 5px;
    list-style-type: none;
    line-height: 10px;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;

    &::after {
        content: " ";
        display: block;
        position: absolute;
        top: 36px;
        left: 0;
        right: 0;
        width: 100%;
        height: 1px;
        background: #d7e0e2;
    }

    & .trumbowyg-button-group {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-flow: row wrap;
        flex-flow: row wrap;

        .trumbowyg-fullscreen-button svg {
