/* Modal box */
.trumbowyg-modal {
    position: absolute;
    top: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    max-width: 520px;
    width: 100%;
    height: 350px;
    z-index: 11;
    overflow: hidden;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden; }

