// Component: Sidebar Mini
//==========================================>

// Add sidebar-mini class to the body tag to activate this feature
.sidebar-mini {
    //Sidebar mini should work only on devices larger than @screen-sm
    @media (min-width: $screen-sm) {
        //When the sidebar is collapsed...
        &.sidebar-collapse {

            // Apply the new margining to the main content and footer
            .content-wrapper,
            .right-side,
            .main-footer {
                margin-left: 50px !important;
                z-index: 840;
            }

            // Modify the sidebar to shrink instead of disappearing
            .main-sidebar {
                // Don't go away! Just shrink
                @include translate(0, 0);
                width: 50px !important;
                z-index: 850;
            }

            .sidebar-menu {
                & > li {
                    position: relative;

                    & > a {
                        margin-right: 0;

                        & > span {
                            border-top-right-radius: 4px;
                        }
                    }


                    &:not(.treeview) {
                        & > a > span {
                            border-bottom-right-radius: 4px;
                        }
                    }

                    & > .treeview-menu {
                        //Add some padding to the treeview menu
                        padding-top: 5px;
                        padding-bottom: 5px;
                        border-bottom-right-radius: 4px;
                    }

                    //Show menu items on hover
                    &:hover {
                        & > a {
                            //overflow: visible;
                        }

                        & > a > span:not(.pull-right),
                        & > .treeview-menu {
                            display: block !important;
                            position: absolute;
                            width: $sidebar-width - 50;
                            left: 50px;
                        }

                        //position the header & treeview menus
                        & > a > span {
                            top: 0;
                            margin-left: -3px;
                            padding: 12px 5px 12px 20px;
                            background-color: inherit;
                        }

                        & > .treeview-menu {
                            top: 44px;
                            margin-left: 0;
                        }
                    }
                }
            }

            // Make the sidebar links, menus, labels, badges and angle icons disappear
            .main-sidebar .user-panel > .info,
            .sidebar-form,
            .sidebar-menu > li > a > span,
            .sidebar-menu > li > .treeview-menu,
            .sidebar-menu > li > a > .pull-right,
            .sidebar-menu li.header {
                display: none !important;
                @include translateZ(0);
            }

            .main-header {
                // Let's make the logo also shrink and the mini logo to appear
                .logo {
                    width: 50px;

                    & > .logo-mini {
                        display: block;
                        margin-left: -15px;
                        margin-right: -15px;
                        font-size: 18px;
                    }

                    & > .logo-lg { display: none; }
                }

                // Since the logo got smaller, we need to fix the navbar's position
                .navbar { margin-left: 50px; }
            }
        }
    }
}

//A fix for text overflow while transitioning from sidebar mini to full sidebar
.sidebar-menu,
.main-sidebar .user-panel,
.sidebar-menu > li.header {
    white-space: nowrap;
    overflow: hidden;
}

.sidebar-menu:hover {
    overflow: visible;
}

.sidebar-form,
.sidebar-menu > li.header {
    overflow: hidden;
    text-overflow: clip;
}

.sidebar-menu li > a {
    position: relative;

    & > .pull-right {
        position: absolute;
        right: 10px;
        top: 50%;
        margin-top: -7px;
    }
}
