// Component: Main Header
//==========================================>

.main-header {
    position: relative;
    max-height: 100px;
    z-index: 1030;

    // Navbar
    & > .navbar {
        margin-bottom: 0;
        margin-left: $sidebar-width;
        border: none;
        min-height: $navbar-height;
        @include border-radius(0);
        @include transition(margin-left $transition-speed $transition-fn);

        .layout-top-nav & {
            margin-left: 0;
        }
    }

    // Navbar search text input
    #navbar-search-input.form-control {
        background: rgba(255, 255, 255, .2);
        border-color: transparent;

        &:focus, &:active {
            border-color: rgba(0, 0, 0, .1);
            background: rgba(255, 255, 255, .9);
        }

        &::-moz-placeholder {
            color: #ccc;
            @include opacity(1);
        }

        &:-ms-input-placeholder {
            color: #ccc;
        }

        &::-webkit-input-placeholder {
            color: #ccc;
        }
    }

    // Navbar Right Menu
    .navbar-custom-menu,
    .navbar-right {
        float: right;

       @media (max-width: $screen-sm-max) {
            a {
                color: inherit;
                background: transparent;
            }
        }
    }

    .navbar-right {
       @media (max-width: $screen-header-collapse) {
            float: none;
            .navbar-collapse & {
                margin: 7.5px -15px;
            }

            & > li {
                color: inherit;
                border: 0;
            }
        }
    }

    // Navbar toggle button
    .sidebar-toggle {
        float: left;
        background: transparent none;
        padding: $navbar-padding-vertical $navbar-padding-horizontal;
        // Add the fontawesome bars icon
        font-family: FontAwesome, sans-serif;

        &:before { content: "\f0c9"; }
        &:hover  { color: #fff; }
        &:focus, &:active {
            background: transparent;
        }
    }

    .sidebar-toggle .icon-bar {
        display: none;
    }

    .navbar .nav {
        // Navbar User Menu
        & > li.user > a {
            & > .fa, & > .glyphicon, & > .ion {
                margin-right: 5px;
            }
        }

        // Labels in navbar
        & > li > a > .label {
            position: absolute;
            top: 9px;
            right: 7px;
            padding: 2px 3px;
            text-align: center;
            font-size: 9px;
            line-height: .9;
        }
    }

    //Logo bar
    .logo {
        display: block;
        float: left;
        height: $navbar-height;
        font-size: 20px;
        line-height: 50px;
        text-align: center;
        width: $sidebar-width;
        font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
        padding: 0 15px;
        font-weight: 300;
        overflow: hidden;
        @include transition(width $transition-speed $transition-fn);
        // Add support to sidebar mini by allowing the user to create 2 logo designs. mini and lg

        .logo-lg  { display: block; } // should be visibile when sidebar isn't collapsed
        .logo-mini { display: none; }
    }

    // Navbar Brand. Alternative logo with layout-top-nav
    .navbar-brand { color: #fff; }
}

// Content Header
.content-header {
    position: relative;
    padding: 15px;

    // Header Text
    & > h1 {
        margin: 0;
        font-size: 24px;

        & > small {
            font-size: 15px;
            display: inline-block;
            padding-left: 4px;
            font-weight: 300;
        }
    }

    & > .breadcrumb {
        float: right;
        background: transparent;
        margin-top: 0;
        margin-bottom: 0;
        font-size: 12px;
        padding: 7px 5px;
        position: absolute;
        top: 15px;
        right: 10px;
        @include border-radius(2px);

        & > li {
            & > a {
                color: #444;
                text-decoration: none;
                display: inline-block;

                & > .fa, & > .glyphicon, & > .ion {
                    margin-right: 5px;
                }
            }

            & + li:before { content: '>\00a0'; }
        }
    }

   @media (max-width: $screen-sm-max) {
        & > .breadcrumb {
            position: relative;
            margin-top: 5px;
            top: 0;
            right: 0;
            float: none;
            background: $light-gray;
            padding-left: 10px;

            li:before { color: darken($light-gray, 20%); }
        }
    }
}

.navbar-toggle {
    color: #FFF;
    border: 0;
    margin: 0;
    padding: $navbar-padding-vertical $navbar-padding-horizontal;
}

// Control navbar scaffolding on x-small screens
@media (max-width: $screen-sm-max) {
    .navbar-custom-menu {
        // Don't let links get full width
        .navbar-nav {
            margin: 0;
            float: left;

            & > li {
                float: left;

                & > a {
                    padding-top: 15px;
                    padding-bottom: 15px;
                    line-height: 20px;
                }
            }
        }
    }
}

// Collapse header
@media (max-width: $screen-header-collapse) {
    .main-header {
        position: relative;

        .logo, .navbar {
            width: 100%;
            float: none;
        }

        .navbar { margin: 0; }
        .navbar-custom-menu { float: right; }
    }
}

.navbar-collapse.pull-left {
   @media (max-width: $screen-sm-max) {
        float: none !important;

        & + .navbar-custom-menu {
            display: block;
            position: absolute;
            top: 0;
            right: 40px;
        }
    }
}
